nav {
  width: 100%;
  position: fixed;
  z-index: 888; }
  nav a {
    color: #eee;
    text-decoration: none;
    line-height: 60px;
    display: block;
    padding: 0 15px; }
  nav .collapse a:hover {
    background-color: #eee;
    color: #333; }
  nav .hamburger {
    display: block;
    width: 50px;
    height: 50px;
    background: transparent;
    position: relative;
    text-align: center;
    border-radius: 5px;
    float: right; }
    nav .hamburger span {
      display: inline-block;
      background: #eee;
      width: 40px;
      height: 4px;
      position: absolute;
      border-radius: 20px;
      left: 0;
      top: 10px;
      bottom: 0;
      right: 10px;
      margin: auto; }
      nav .hamburger span:before {
        content: "";
        display: inline-block;
        background: #eee;
        width: 40px;
        height: 4px;
        position: absolute;
        border-radius: 20px;
        left: 0;
        top: -11px; }
      nav .hamburger span:after {
        content: "";
        bottom: -12px;
        display: inline-block;
        background: #eee;
        width: 40px;
        height: 4px;
        position: absolute;
        border-radius: 20px;
        left: 0; }

