footer {
  height: 50px; }
  footer #gotop {
    display: none;
    position: fixed;
    width: 62px;
    height: 62px;
    background: #333;
    right: 30px;
    bottom: 30px;
    text-align: center;
    line-height: 85px;
    text-decoration: none;
    border-radius: 50%;
    color: #eee;
    letter-spacing: 2px;
    font-weight: 600;
    transition: .4s;
    z-index: 999; }
    footer #gotop:hover {
      color: white;
      background: rgba(0, 0, 0, 0.8); }
    footer #gotop:before {
      content: '';
      position: absolute;
      top: 15px;
      right: 22px;
      width: 10px;
      height: 10px;
      border-top: 5px solid #eee;
      border-left: 5px solid #eee;
      transform: rotate(45deg);
      animation: up 1s infinite; }

@keyframes up {
  0%, 50%, 100% {
    transform: translateY(10%) rotate(45deg); }
  25%, 75% {
    transform: translateY(-20%) rotate(45deg); } }

